<template>
    <div>
        <div class="blog">
            <div class="container">
                <div class="row">
                    <div class="col-lg-10 offset-lg-1">
                        <div class="blog-tags">
                            <span v-for="(tag, index) of blogTag" :key="index">{{tag}}</span>
                        </div>
                        <hr>
                    </div>
                </div>
                <div class="row">
                    <div class="col-lg-7 offset-lg-1">
                        <div class="blog-list" key="i">
                            <div class="blog-item" v-for="(blog, index) in blogData" :key="index">
                                <h4><router-link :to="`/Blog/${blog.blogId}`">{{blog.title}}</router-link></h4>
                                <p>{{blog.desc}}</p>
                                <span>更新日期: {{blog.updated_dt}}</span>
                            </div>
                            <div class="blog-empty" v-if="!blogData.length">
                                <p>Nothing Here.</p>
                            </div>
                            <hr>
                            <div class="blog-pagination">
                                <a class="blog-paging-prev" href="#">&lt; 上一页</a>
                                <a class="blog-paging-next" href="#">下一页 &gt;</a>
                            </div>
                        </div>
                    </div>
                    <div class="col-lg-3">
                        <ul class="blog-related">
                            <li>相关博客</li>
                            <li><a href="#">博客1</a></li>
                            <li><a href="#">博客2</a></li>
                            <li><a href="#">博客3</a></li>
                            <li><a href="#">博客4</a></li>
                            <li><a href="#">博客5</a></li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            blogTag: [],
            blogData: {},
            blogPerPage: 5
        }
    },
    mounted() {
        this.$axios.get("/static/mock/index.json").then(res => {
            this.blogData = res.data.blog
            this.blogTag = res.data.blogTag
        })
    },
    methods: {
        
    }
}
</script>

<style scoped>
    .blog {
        margin: 2rem 0;
    }
    .blog-tags {
        display: flex;
        flex-wrap: wrap;
        gap: 5px;
    }
    .blog-tags span {
        padding: 16px 20px;
        background: #000;
        color: #fff;
        cursor: pointer;
        user-select: none;
    }
    hr {
        opacity: 1;
    }
    .blog-item {
        background: #f1f1f1;
        padding: 1rem;
        margin-bottom: 1rem;
    }
    .blog-item:last-of-type {
        margin-bottom: 0;
    }
    .blog-item p {
        margin: 0;
        display: -webkit-box;
        overflow: hidden;
        text-overflow: ellipsis;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
    }
    .blog-item span {
        display: block;
        text-align: right;
        font-size: 0.8rem;
    }
    .blog-pagination {
        display: flex;
        justify-content: space-between;
    }
    .blog-empty {
        padding: 15rem 0;
        text-align: center;
    }
    .blog-empty p {
        margin: 0;
    }
    .side-list {
        position: sticky;
        top: 2rem;
    }
    .blog-related {
        list-style: none;
        padding: 0;
        border: 1px solid #f1f1f1;
    }
    .blog-related li a {
        display: -webkit-box;
        overflow: hidden;
        text-overflow: ellipsis;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        max-width: 100%;
    }
    .blog-related li {
        border-bottom: 1px solid #f1f1f1;
        padding: 0.5rem 1rem;
    }
    .blog-related li:first-of-type {
        background: #f1f1f1;
    }
    .blog-related li:last-of-type {
        border-bottom: none;
    }
</style>


